<template>
<div class="main">
  <canvas v-show="false" id="loginAni"></canvas>
  <div class="logo">
    <img src="../../../../../assets/logo.png" alt="">
    <span>让家居完美到家</span>
  </div>
  <div class="login-wrapper">
    <div class="img">
      <img src="../../../../../assets/left.png" alt="">
    </div>
      <div class="login-block" >
          <div class="shodow1"></div>
          <div class="shodow2"></div>
          <div class="form-content">
            <div class="sys-logo">OKR管理系统</div>
            <el-form class="login-form" ref="loginForm" :model="loginForm" :rules="loginRules" size="medium">
              <el-form-item prop="userAccount">
                  <el-input
                    class="login-input"
                    type="text"
                    v-model="loginForm.userAccount"
                    placeholder="用户名">
                                        <i slot="prefix" class="fdddfont icon-personstalker"></i>
                  </el-input>
              </el-form-item>

              <el-form-item prop="userPassword">
                <el-input
                  class="login-input"
                  type="password"
                  v-model="loginForm.userPassword"
                  @keyup.enter.native="login('loginForm')"
                  placeholder="密码">
                                    <i slot="prefix" class="fdddfont icon-password"></i>

                </el-input>
              </el-form-item>
              <el-form-item >
                <div style="margin-top:8px;">
                  <el-button class="login-button" type="primary" :loading="isLoginning" @click="login('loginForm')">登录</el-button>
                  <!-- <el-button @click="resetForm('loginForm')">重置</el-button> -->
                </div>
              </el-form-item>
            </el-form>
          </div>

      </div>
  </div>
</div>
</template>

<script>
import { startAnimation } from './loginAni'

export default {
  name: 'Login',
  data () {
    return {
      isLoginning: false,
      loginForm: {
        userAccount: '',
        userPassword: '',
        sysType: '5'
      },
      loginRules: {
        userAccount: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        userPassword: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ]
      }
    }
  },
  mounted () {
    startAnimation('loginAni')
  },
  methods: {
    login (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.isLoginning = true
          this.$store.dispatch('userLogin', {...this.loginForm}).then(() => {
            this.$router.push('/dash')
          }).finally(() => {
            this.isLoginning = false
          })
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="scss" scoped>
.main {
  display:flex;
  justify-content: center;
  width:100%;
  height:100%;
  // background: url('../../../../../assets/bg.png') no-repeat center fixed;
  // background-size: cover;
  background-color:#202A3A;
  height:100vh;
  position:relative;
  .logo{
    width:100%;
    border-bottom: 1px solid white;
    color:white;
    height:4rem;
    line-height:4rem;
    padding-left:1rem;
    text-align:left;
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items:center;
    span{
      margin-left:2rem;
      font-size:1.6rem;
    }
    img{
      height:2rem;
    }
  }
  .login-wrapper{
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);

    min-height:300px;

    z-index:10;
    display:flex;
    justify-content: center;
    align-items: center;
    padding:7rem 7.5rem;
    .img{
      img{
        width: 639px ;
      }
    }
    .login-block{
      display: flex;
      justify-content: center;
      flex-direction: column;
      height:270px;
      margin-left:8.5rem;
      position:relative;
      .shodow1{
        background-color: #eeeeeeb3;
        width: 362px;
        height: 300px;
        position: absolute;
        left: -20px;
        border-radius: 1rem;
        z-index: 2;
      }
      .shodow2{
        background-color: #f4f4f547;
        width: 402px;
        height: 260px;
        position: absolute;
        left: -40px;
        border-radius: 0.5rem;
        z-index: 1;
      }

      .form-content{
        padding:70px 50px ;
        box-shadow:0 0 45px rgba(0,0,0,.18);
        border:1px solid white;
        background-color:white;
        border-radius:2rem;
        z-index:10;
        .sys-logo {
          font-size:1.5rem;
          text-align: center;
        }

        .login-form {
          padding-top:25px;

          .login-input{
            width:220px;
          }

          .login-button {
            width:100%;
            border-radius: 18px;
            background-color:#202A3A !important;
            color:white !important;
          }

      }
      }



    }

  }
}

</style>



